body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background-color: hsl(30, 38%, 92%);
  width: 1440px;
  font-family: 'Montserrat', sans-serif;
}

#root div{
  display: grid;
  grid-template-columns: 3fr 1fr 1fr;
  width: 50%;
  background-color: white;
  border-radius: 25px;
  margin: 5% auto;
  column-gap: 5%;
  padding-right: 2%;
}

.product{
  grid-row: span 5;
  width: 100%;
  border-radius: 25px 0 0 25px;
}

#root div h1{
  grid-column: span 2;
  font-size: 20px;
  letter-spacing: 5px;
  color: hsl(228, 12%, 48%);
  font-weight: 400;
  margin: 10% 0 0 0;
}

#root div h2{
  grid-column: span 2;
  font-family: 'Fraunces', sans-serif;
  font-size: 40px;
  font-weight: 700;
  color: hsl(212, 21%, 14%);
  margin: 0;
  line-height: 40px;
}

#root div p{
  grid-column: span 2;
  margin: 0;
  color: hsl(228, 12%, 48%);
  line-height: 25px;
  padding-right: 5%;
}

#root div h3{
  font-size: 35px;
  font-family: 'Fraunces', sans-serif;
  color: hsl(158, 36%, 37%);
  margin: 0;
}

#root div h4{
  color: hsl(228, 12%, 48%);
  text-decoration: line-through;
  margin: 0;
  padding-top: 10%;
}

#root div button{
  grid-column: span 2;
  font-family: 'Montserrat', sans-serif;
  background-color: hsl(158, 36%, 37%);
  border-style: none;
  border-radius: 10px;
  color: white;
  font-size: 15px;
  cursor: pointer;
  transition-duration: 0.5ms;
  margin-bottom: 10%;
}

#root div button img{
  margin-right: 5%;
}

#root div button:hover{
  background-color: hsl(158, 37%, 27%);
}

@media (max-width: 375px){
  body{
    width: 375px;
  }

  #root div{
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    padding: 0;
    width: 90%;
    position: relative;
  }

  .product{
    content: url("images/image-product-mobile.jpg");
    border-radius: 10px 10px 0 0;
  }

  #root div h1{
    font-size: 15px;
    margin: 5%;
  }

  #root div h2{
    font-size: 30px;
    margin: 0 5% 5% 5%;
    line-height: 30px;
  }

  #root div p{
    font-size: 15px;
    margin: 0 5% 5% 5%;
  }

  #root div h3{
    margin: 0 5% 5% 5%;
    font-size: 30px;
  }

  #root div h4{
    position: absolute;
    left: 50%;
    top: 76%;
    font-weight: 400;
  }

  #root div button{
    margin: 0 5% 5% 5%;
    border-radius: 10px;
    height: 50px;
  }
}